import axios from "axios";
import { gitCreds } from '../assets/configurations';

const getPinnedRepo = async () => {
  const result = await axios({
    url: 'https://api.github.com/graphql',
    headers: { Authorization: 'Bearer ' + gitCreds.githubConvertedToken },
    method: 'post',
    data: {
      query: `
            {
              user(login: "${gitCreds.githubUserName}") {
                pinnedItems(first: 6, types: [REPOSITORY, GIST]) {
                  totalCount
                  edges {
                    node {
                      ... on Repository {
                        openGraphImageUrl
                        description
                        url
                        name
                        updatedAt
                        stargazers{
                          totalCount
                        }
                        languages(first:10) {
                          edges {
                            node {
                              id
                              name
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }           
      `
    }
  })
  return result;
};

const parseResult = result => {
  let finalRes = [];
  for(let obj of result){
    finalRes.push({
      title: obj.node.name,
      desc: obj.node.description,
      image: obj.node.openGraphImageUrl,
      stars: obj.node.stargazers.totalCount,
      languages: obj.node.languages.edges.map(data => {return data.node.name}),
      date: obj.node.updatedAt,
      link: obj.node.url
    });
  }
  return finalRes;
}

const getPinnedRepoData = async () => {
  const result = await getPinnedRepo();
  const parsedResult = parseResult(result.data.data.user.pinnedItems.edges);
  return parsedResult;
}

export default getPinnedRepoData;