import React, { Component } from "react";
import { Container } from "react-bootstrap";
import { BrowserRouter as Router, Route } from "react-router-dom";
import ParrotFooter from "./parrotComponents/ParrotFooter";
import ParrotNavbar from "./parrotComponents/ParrotNavbar";
import ParrotAbout from "./parrotPages/ParrotAbout";
import ParrotContact from "./parrotPages/ParrotContact";
import ParrotHome from "./parrotPages/ParrotHome";
import ParrotProjects from "./parrotPages/ParrotProjects";
import "./static/css/main.scss"; // All of our styles
import getPinnedRepoData from "./services/gitService";
import ParrotResume from "./parrotPages/ParrotResume";

export class App extends Component {
  async componentDidMount() {
    let repoData = await getPinnedRepoData();
    this.setState({ repoData });
  }

  constructor(props) {
    super(props);
    this.state = {
      repoData: [],
      title: "Harshil Darji",
      headerLinks: [
        { title: "home", path: "/" },
        { title: "about", path: "/about" },
        { title: "contact", path: "/contact" },
      ],
      home: {
        title: "Harshil Darji",
        subtitle: [
          "Web Developer",
          "Cloud Enthusiast",
          "Linux Fan",
          "Open Source Contributor",
          "Gamer",
        ],
        text: "Glad you found your way here!",
      },
      about: {
        title: "About Me",
      },
      contact: {
        title: "Let's Talk",
        subtitle: "You can email me directly from here.",
      },
    };
  }
  render() {
    return (
      <Router>
        <div style={{ backgroundColor: "#212329" }}>
          <Container className="p-0" fluid={true}>
            <ParrotNavbar />
            <Route path="/" exact render={() => <ParrotHome />} />
            <Route path="/about" exact render={() => <ParrotAbout />} />
            <Route
              path="/projects"
              exact
              render={() => <ParrotProjects data={this.state.repoData} />}
            />
            <Route path="/resume" exact render={() => <ParrotResume />} />
            <Route path="/contact" exact render={() => <ParrotContact />} />
          </Container>
          <ParrotFooter />
        </div>
      </Router>
    );
  }
}

export default App;
