import React from "react";
import { Col, Container } from "react-bootstrap";
import ParrotIcons from "./ParrotIcons";

function ParrotFooter() {
  return (
    //TODO: Adjust footer ffs
    <footer id="footer">
      <Container
        fluid={true}
        className="border-top justify-content-between p-3"
      >
        <Col className="p-0">
          <ParrotIcons size="sm" />
        </Col>
      </Container>
    </footer>
  );
}

export default ParrotFooter;
